import React from 'react';
import { graphql, StaticQuery } from 'gatsby';
import useSiteMetadata from "../hooks/use-site-metadata"
import { Button, Link } from '@theme-ui/components'
import replaceSlashes from "../utils/replaceSlashes"
import kebabCase from "lodash.kebabcase"

export default () => {
    const { tagsPath, basePath } = useSiteMetadata()

    return <StaticQuery
        query={graphql`
            query{
                allPost(sort: { fields: tags___name, order: DESC }, filter: {draft: {ne: true}}) {
                    group(field: tags___name) {
                      fieldValue
                      totalCount
                    }
                  }
            }
        `}
        render={data => (
            <>
            {data.allPost.group.map(item => (
                <Link href={replaceSlashes(`/${basePath}/${tagsPath}/${kebabCase(item.fieldValue)}`)}>
                    <Button mr={2} variant='secondary' sx={{cursor: 'pointer'}}>{item.fieldValue} ({item.totalCount})</Button>
                </Link>
            ))}
            </>
        )}
    />
}